.home-screen {
  .aside {
    display: block;
    background-color: $bg-light;
    font-size: 16px;
    padding: 0px $wrapper-mobile-padding;
    padding-top: 40px;
    padding-bottom: 56px;
    position: relative;

    &__text-new {
      font-family: $lato;
      font-weight: bold;
      font-size: 0.625em;
      line-height: 1.6em;
      text-transform: uppercase;
      color: $text-dark;
      z-index: 2;
      position: relative;
    }
    
    &__title {
      font-family: $lato;
      font-weight: normal;
      color: $text-dark;
      font-size: 1.75em;
      line-height: 1.29em;
      letter-spacing: -0.6px;
      margin-top: 8px;
      z-index: 2;
      position: relative;

    }

    &__text-description {
      font-family: $roboto;
      color: $text-light;
      font-size: 0.875em;
      line-height: 1.71em;
      margin-top: 12px;
      z-index: 2;
      position: relative;
    }

    &__character {
      display: block;
      height: 240px;
      width: auto;
      position: absolute;
      bottom: -20px;
      right: -38px;
      z-index: 1;
    }
  }

  .main {
    overflow: hidden;
    padding: 0px $wrapper-mobile-padding;

    .form__tyc-copy {
      margin-left: 12px;
      font-family: $roboto;
      font-weight: 300;
      color: $text-light;
      
      a {
        font-weight: normal;
        color: $text-light;
        line-height: 20px;
      }
    }

    .form__send-button {
      margin-top: 16px;
      margin-bottom: 16px;
    }

    h2.main__title {
      margin: 40px 0 24px 0;
      color: $text-dark;
      font-family: $lato;
      font-style: normal;
      font-weight: normal;
      font-size: 24px;
      line-height: 32px;
    }

  }
  
  @media screen and (min-width: $mobile-breakpoint) {
    display: grid;
    grid-template-columns: 14fr 23fr;
    grid-template-rows: auto;
    height: 100%;
    width: 100%;
    max-width: $container-max-width;
    margin: auto;

    .aside {
      position: relative;
      font-size: 21px;
      &__container {
        width: 320px;
        margin: 0 6.56%
      }
      &__character {
        display: block;
        height: auto;
        width: 88%;
        position: relative;
        bottom: initial;
        right: initial;
      }
      &:after {
        content: '';
        display: block;
        position: absolute;
        right: 0;
        top: 0;
        height: 100%;
        width: 100vw;
        background-image: url('../../assets/background-home.png');
        background-size: cover;
        background-position: right;
      }
    }

    .main {
      &__container {
        width: 288px;
        margin: auto;
      }
    }

    
  }
}